import React from "react";
import { Link } from "react-router-dom";

function Navbar() {
  const handleClick = () => {
    const listItem = document.querySelector("#nav");
    // if (listItem.classList.contains("hidden")) {
    //   listItem.style.position = "fixed";
    //   listItem.style.top = "40px";
    //   listItem.style.left = "25px";
    // }

    listItem.classList.contains("hidden")
      ? listItem.classList.remove("hidden")
      : listItem.classList.add("hidden");
  };

  return (
    <>
      <div
        className="hidden fixed top-10 left-16 text-xs lg:block lg:static sm:text-base"
        id="nav"
      >
        <Link className="text-xl md:text-2xl mr-4 hover:text-red-500" to="/modelshub">
          Home
        </Link>
        <Link
          className=" text-xl md:text-2xl ml-4 hover:text-red-500"
          to="/about"
        >
          About
        </Link>
        <Link
          className="ml-4 hover:text-red-500 text-xl md:text-2xl"
          to="/gridgallery"
        >
          theWALL
        </Link>
      </div>
      <div
        className=" text-red-600 z-10 font-bold cursor-pointer burger fixed top-5 right-9 lg:hidden w-12 h-4"
        onClick={handleClick}
      >
        toggle
      </div>
    </>
  );
}

export default Navbar;
